.navbar {
  background: #333;
  height: 80px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.menu-bars {
  color: #eee;
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
}

.nav-menu {
  background: #333;
  width: 250px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: all 0.4s ease;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 8px 0 8px 16px;
  list-style: none;

  height: 60px;
}

.nav-text a {
  text-decoration: none;
  color: #eee;
  font-size: 20px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.nav-text a:hover {
  background: rgba(0, 0, 0, 0.267);
}

.nav-menu-items {
  width: 100%;
}

.navbar-toggle {
  background-color: #333;
  width: 95%;
  height: 80px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

span {
  margin-left: 16px;
}
